require('./main.css');
import MDHierarchy from "./hierarchy/hierarchy";
import MenuTransformer from "./navigation/menu-transformer";

function upgradeHierarchy(){
  var hierarchyUpgrade = new MDHierarchy();
  hierarchyUpgrade.upgrade();
}

window.addEventListener('DOMContentLoaded',()=>{
  upgradeHierarchy()

  window.Reportal = window.Reportal || {};
  window.Reportal.state = window.Reportal.state || {};
  window.Reportal.state.menuTransformer = new MenuTransformer();
});



